header {
    height: 72px;
    left: 0px;
    top: 0px;
    border-radius: 0px;
  
  
    position: absolute;
    left: 0%;
    right: 0%;
    top: 0%;
    bottom: 0%;
  
    background: #0064EB;
  }
  
  .frame {
    height: 40px;
    width: 41.0186767578125px;
    left: 41px;
    top: 16px;
    border-radius: 0px;
  
    position: absolute;
    width: 41.02px;
    height: 40px;
    left: 41px;
    top: 16px;
  
  }
  
  .frame .header-logo {
    height: 40px;
    width: 41.0186767578125px;
    left: 41px;
    top: 16px;
